class HttpError {
    constructor(errorCode, errorMsg, error){
        this.errorCode = errorCode;
        this.errorMsg = errorMsg;
        this.error = error;
    }

}

const HTTP_ERROR_CONSTANT = {
    // 400 ~ 415 
    BadRequest: new HttpError(400, 'Bad Request '),
    Unauthorized: new HttpError(401, 'Unauthorized ,'),
    Forbidden: new HttpError(403, 'Forbidden ,'),
    NotFound: new HttpError(404, 'Not Found '),
    MethodNotAllowed : new HttpError(405, 'Method Not Allowed GETPOST'),
    NotAcceptable: new HttpError(406, 'Not Acceptable MIMEAccpet'),   
    UnsupportedMedia: new HttpError(415, 'Unsupported Media '),
    BadRequestMin: new HttpError(9400, 'Bad Request , '),
    BadRequestMax: new HttpError(9499, 'Bad Request , '),

    // 500 ~ 599 
    ServerError: new HttpError(500, 'Server Error '),
    NotImplemented: new HttpError(501, 'Not Implemented '),
    ServiceUnavailable: new HttpError(502, 'Service Unavailable '),
    GatewayTimeout: new HttpError(504, 'Gateway Timeout '),
    HTTPVersionNotSupported: new HttpError(505, 'HTTP Version Not Supported HTTP'),
    ServerExceptionMin: new HttpError(9500, 'Server Exception '),
    ServerExceptionMax: new HttpError(9599, 'Server Exception '),
    
    // 600 ~ 
    UnkownError: new HttpError(600, 'Unkown Error ,  '),
}

export {
    HttpError,
    HTTP_ERROR_CONSTANT
} 
